___CSS_LOADER_EXPORT___.push([module.id, `body {
  margin: 2%;
  padding: 5%;
  color: #fff;
  background-color: #444654;
}

.flex-row {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.flex-col {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

h1 {
  font-family: Brush Script MT, cursive;
  color: rgb(8, 8, 15);
  padding-left: 20%;
}

h4 {
  margin-right: 5%;
}

#container {
  width: 100%;
}

#score-heading {
  width: 100%;
}

#score-ul {
  border: 1px solid rgba(255, 51, 255, 0.2);
  min-width: 15rem;
  min-height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 2%;
  padding-right: 5%;
}

li:nth-child(even) {
  background-color: #f0bcbc;
}

li:nth-child(odd) {
  background-color: #fff;
}

#score-ul li {
  list-style-type: none;
  width: 100%;
  color: #020202;
  padding: 2%;
}

#add-scores {
  align-items: flex-start;
  justify-content: space-around;
}

input {
  padding: 2%;
  margin-bottom: 5%;
}

#refresh,
#submit {
  border-radius: 5%;
  box-shadow: 4px -3px teal;
}

#submit {
  align-self: flex-end;
}

label {
  color: #fff;
}

#temp-text {
  text-align: center;
  font-weight: 100;
  text-decoration: wavy;
  width: 100%;
}`, "",{"version":3,"sources":["webpack://./src/styles/style.css"],"names":[],"mappings":"AAAA;EACE,UAAA;EACA,WAAA;EACA,WAAA;EACA,yBAAA;AACF;;AAEA;EACE,aAAA;EACA,mBAAA;EACA,6BAAA;AACF;;AAEA;EACE,aAAA;EACA,sBAAA;EACA,8BAAA;EACA,mBAAA;AACF;;AAEA;EACE,qCAAA;EACA,oBAAA;EACA,iBAAA;AACF;;AAEA;EACE,gBAAA;AACF;;AAEA;EACE,WAAA;AACF;;AAEA;EACE,WAAA;AACF;;AAEA;EACE,yCAAA;EACA,gBAAA;EACA,iBAAA;EACA,aAAA;EACA,sBAAA;EACA,2BAAA;EACA,uBAAA;EACA,WAAA;EACA,iBAAA;AACF;;AAEA;EACE,yBAAA;AACF;;AAEA;EACE,sBAAA;AACF;;AAEA;EACE,qBAAA;EACA,WAAA;EACA,cAAA;EACA,WAAA;AACF;;AAEA;EACE,uBAAA;EACA,6BAAA;AACF;;AAEA;EACE,WAAA;EACA,iBAAA;AACF;;AAEA;;EAEE,iBAAA;EACA,yBAAA;AACF;;AAEA;EACE,oBAAA;AACF;;AAEA;EACE,WAAA;AACF;;AAEA;EACE,kBAAA;EACA,gBAAA;EACA,qBAAA;EACA,WAAA;AACF","sourcesContent":["body {\n  margin: 2%;\n  padding: 5%;\n  color: #fff;\n  background-color: #444654;\n}\n\n.flex-row {\n  display: flex;\n  align-items: center;\n  justify-content: space-evenly;\n}\n\n.flex-col {\n  display: flex;\n  flex-direction: column;\n  justify-content: space-between;\n  align-items: center;\n}\n\nh1 {\n  font-family: Brush Script MT, cursive;\n  color: rgb(8, 8, 15);\n  padding-left: 20%;\n}\n\nh4 {\n  margin-right: 5%;\n}\n\n#container {\n  width: 100%;\n}\n\n#score-heading {\n  width: 100%;\n}\n\n#score-ul {\n  border: 1px solid #f3f3;\n  min-width: 15rem;\n  min-height: 100px;\n  display: flex;\n  flex-direction: column;\n  justify-content: flex-start;\n  align-items: flex-start;\n  padding: 2%;\n  padding-right: 5%;\n}\n\nli:nth-child(even) {\n  background-color: #f0bcbc;\n}\n\nli:nth-child(odd) {\n  background-color: #fff;\n}\n\n#score-ul li {\n  list-style-type: none;\n  width: 100%;\n  color: #020202;\n  padding: 2%;\n}\n\n#add-scores {\n  align-items: flex-start;\n  justify-content: space-around;\n}\n\ninput {\n  padding: 2%;\n  margin-bottom: 5%;\n}\n\n#refresh,\n#submit {\n  border-radius: 5%;\n  box-shadow: 4px -3px teal;\n}\n\n#submit {\n  align-self: flex-end;\n}\n\nlabel {\n  color: #fff;\n}\n